import { StaticDatePicker } from '@mui/lab';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';

import { useState } from 'react';
import { Card, CardHeader, TextField, Typography, Box } from '@mui/material';

export default function CalendarCard() {
    const [value, setValue] = useState(new Date());
    const options = { weekday: 'short', month: 'short', day: 'numeric' };
    return (
        <Card className="card calendar-card" sx={{ minHeight: 450, width: 475 }}>
            <CardHeader
                title={<Typography component="span" variant="h4" >{value.toLocaleDateString("en-US", options)}</Typography>}
                sx={{
                    paddingY: 2,
                    height: 60,
                    backgroundColor: "#00AB55", color: "#fff"
                }}
            />
            <Box sx={{ mt: 2 }}>
                <LocalizationProvider dateAdapter={AdapterDateFns}>
                    <StaticDatePicker

                        displayStaticWrapperAs="desktop"
                        openTo="day"
                        value={value}
                        orientation="landscape"
                        onChange={(newValue) => {
                            setValue(newValue);
                        }}
                        renderInput={(params) => <TextField {...params} />}
                    />
                </LocalizationProvider>
            </Box>
        </Card >
    );
}