import CustomElement from './CustomElement';
import CalendarCard from '../components/CalendarCard';
const ATTRIBUTES = {
  serviceUrl: 'service-url',
  locale: "locale"
};

class CalendarCardElement extends CustomElement {
  static TAG = 'dw-calendar-card'; // kebab-case

  constructor() {
    super(ATTRIBUTES, CalendarCardElement);
  }

  get children() {
    const attrs = this.getAllAttributes();
    return <CalendarCard {...attrs} />
  }

  static get observedAttributes() {
    return Object.values(ATTRIBUTES);
  }
}

if (!customElements.get(CalendarCardElement.TAG)) {
  customElements.define(CalendarCardElement.TAG, CalendarCardElement);
}